@use "../global/variables.scss";

.modal {
  text-align: center;
  border: 1px solid #c6c6c6;
  background-color: #ffffff;
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
  width: clamp(320px, 72.4vw, 567px);
}

.modal__title {
  color: #000000;
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
  text-transform: uppercase;
  padding-bottom: 14px;

  @media (min-width: variables.$screen-tablet) {
    font-size: 30px;
    padding-bottom: 22px;
  }
}

.modal__description {
  color: #444444;
  font-size: 15px;
  line-height: 30px;
  padding-bottom: 14.5px;

  @media (min-width: variables.$screen-tablet) {
    font-size: 17px;
  }
}
