@charset "UTF-8";
body {
  margin: 0;
}

img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  display: block;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Open_Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/open_sans_regular.woff2") format("woff2"), url("../fonts/open_sans_regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open_Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/open_sans_bold.woff2") format("woff2"), url("../fonts/open_sans_bold.woff") format("woff");
  font-display: swap;
}
.description {
  margin: 0;
  padding: 0;
}

.link {
  display: block;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.title {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  padding: 0 30px;
  margin: 0 auto;
  text-align: start;
  max-width: 1150px;
}
@media (min-width: 768px) {
  .wrapper {
    padding: 0 50px;
  }
}

.page {
  height: 100%;
}

.page__body {
  height: 100%;
  font-family: "Open_Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  margin: 0;
  text-wrap: balance;
  min-height: 100%;
}

.page__container {
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}

.page__modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 2;
}
.page__modal--closed {
  display: none;
}

.main-nav {
  min-height: 56px;
  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  position: relative;
  z-index: 2;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-nav {
    font-size: 14px;
    line-height: 21px;
  }
}

.main-nav__wrapper {
  width: clamp(320px, 100%, 1150px);
  margin: 0 auto;
  position: relative;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    padding: 0 50px;
  }
}

.main-nav__container {
  padding: 22px 30px;
}
@media (min-width: 768px) {
  .main-nav__container {
    padding: 22px 0 20px;
    position: relative;
    z-index: 1;
  }
}
@media (min-width: 1150px) {
  .main-nav__container {
    margin: 0 auto;
    padding: 9px 0 7px;
    width: fit-content;
  }
}

.main-nav__logo {
  display: block;
  width: fit-content;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .main-nav__logo {
    margin: 0;
  }
}
@media (hover: hover) and (pointer: fine) {
  .main-nav__logo:hover {
    opacity: 0.6;
    transition: 0.2s;
  }
  .main-nav__logo:active {
    opacity: 0.3;
    transition: 0.2s;
  }
}

.main-nav__button {
  display: block;
  margin: 0;
  margin-left: auto;
  padding: 0;
  border: none;
  width: 22px;
  height: 22px;
  background-color: transparent;
  position: absolute;
  top: 28px;
  right: 30px;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}
@media (hover: hover) and (pointer: fine) {
  .main-nav__button:hover {
    opacity: 0.6;
  }
  .main-nav__button:active {
    opacity: 0.3;
  }
}
.main-nav__button--cross {
  background-image: url("../img/stack/sprite.svg#nav_cross");
}
.main-nav__button--burger {
  background-image: url("../img/stack/sprite.svg#nav_burger");
}
.main-nav__button--noscript {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__button {
    display: none;
  }
}

.main-nav__list {
  width: inherit;
  text-align: start;
  position: absolute;
  background-color: #ffffff;
}
.main-nav__list--close {
  display: none;
}
.main-nav__list--noscript {
  display: block;
  position: static;
}
@media (min-width: 768px) {
  .main-nav__list {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: flex-start;
    background-color: inherit;
    color: #000000;
    width: 86.98vw;
    left: 50%;
    transform: translate(-50%, 0%);
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    position: absolute;
    top: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: flex-start;
    background-color: inherit;
    color: #000000;
    width: 86.98vw;
    left: 50%;
    transform: translate(-50%, 0%);
  }
}
@media (min-width: 1150px) {
