    width: 1050px;
  }
}

.main-nav__item {
  position: relative;
}

.main-nav__link {
  width: 100%;
  position: relative;
  padding: 26.5px 30px 28.5px 76px;
  border-top: 1px solid #ececec;
}
.main-nav__link--sale, .main-nav__link--new {
  display: none;
}
.main-nav__link--catalog {
  top: 2px;
}
.main-nav__link--order {
  padding-bottom: 26.5px;
}
.main-nav__link--search::before {
  content: "";
  mask-image: url("../img/stack/sprite.svg#search");
  flex-shrink: 0;
  width: 18px;
  height: 18px;
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: cover;
  background-color: #000000;
  opacity: 0.2;
  display: block;
  position: absolute;
  left: 30px;
  top: 32px;
}
.main-nav__link--cart::before {
  content: "";
  mask-image: url("../img/stack/sprite.svg#cart");
  flex-shrink: 0;
  width: 21px;
  height: 22px;
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: cover;
  background-color: #000000;
  opacity: 0.2;
  display: block;
  position: absolute;
  left: 30px;
  top: 25px;
}
.main-nav__link:last-child {
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-nav__link {
    border-top: none;
    padding: 0;
    width: fit-content;
  }
  .main-nav__link:last-child {
    border-bottom: none;
  }
  .main-nav__link--search {
    order: 1;
    padding: 36px 41px 34px;
    min-height: 88px;
    border-left: 1px solid #ececec;
    border-right: 1px solid #ececec;
    flex-shrink: 0;
  }
  .main-nav__link--search::before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .main-nav__link--cart {
    order: 2;
    padding: 32.5px 0 31.5px 31px;
    display: flex;
    align-items: center;
    gap: 31px;
  }
  .main-nav__link--cart::before {
    position: relative;
    left: 2px;
    top: 0;
  }
  .main-nav__link--secondary {
    flex-basis: 150px;
    padding: 20.5px 0;
    flex-shrink: 0;
    font-size: 15px;
  }
  .main-nav__link--catalog {
    padding-top: 21px;
  }
  .main-nav__link--order {
    padding-top: 23px;
  }
}
@media (min-width: 1150px) {
  .main-nav__link--catalog {
    font-size: 17px;
    line-height: 24px;
    padding: 31.5px 0;
  }
  .main-nav__link--order {
    font-size: 17px;
    line-height: 24px;
    padding: 32.5px 0 31.5px 17px;
  }
  .main-nav__link--sale, .main-nav__link--new {
    display: block;
    font-size: 15px;
    line-height: 24px;
  }
  .main-nav__link--secondary {
    flex-basis: 185px;
  }
  .main-nav__link--top:not(:last-child) {
    border-right: 1px solid #ececec;
  }
  .main-nav__link--hero {
    flex-basis: 179px;
  }
  .main-nav__link--cart::before {
    top: -1px;
  }
}
@media (hover: hover) and (pointer: fine) {
  .main-nav__link:hover {
    transition: 0.2s;
    opacity: 0.6;
  }
  .main-nav__link:hover::before {
    transition: 0.2s;
    background-color: #63d1bb;
    opacity: 1;
  }
  .main-nav__link:active {
    transition: 0.2s;
    opacity: 0.3;
  }
}

