@use "../global/variables.scss";

.about-us__wrapper {
  padding-top: 32.3px;
  max-width: 1150px;

  @media (min-width: variables.$screen-tablet) {
    padding-top: 57.5px;
  }

  @media (min-width: variables.$screen-desktop) {
    padding-top: 70px;
  }
}

.about-us__list {
  padding: 28px 0 62px;

  @media (min-width: variables.$screen-tablet) {
    padding-top: 41.5px;
    padding-bottom: 57px;
  }

  @media (min-width: variables.$screen-desktop) {
    padding: 40px 247px 65px 175px;
  }
}

.about-us__title {
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;

  @media (min-width: variables.$screen-tablet) {
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: variables.$screen-desktop) {
    padding-left: 175px;
    font-size: 53px;
    line-height: 57px;
  }
}

.about-us__item {
  @media (min-width: variables.$screen-tablet) {
    &--money {
      grid-column: 1 / 2;
      grid-row: 2 / 3;
    }

    &--like {
      grid-column: 1 / 2;
      grid-row: 3 / 4;
    }
  }
}
